import { BlockchainService } from '../blockchain';
import { TokenOperations } from './token';
import { DirectPayment } from './direct-payment';
import { SigningService } from '../signing';
import { PAYMENT_CONTRACT_ABI } from '../../config/contract';

/**
 * 统一的中继请求数据格式
 */
export interface RelayedRequestData {
  from: string;
  to: string;
  value: string;
  gas: string;
  nonce: string;
  deadline: string;
  data: string;
  signature?: string;
}

/**
 * 中继支付类
 * 负责处理代付gas的支付操作，返回统一格式的签名数据
 */
export class RelayedPayment {
  private blockchainService: BlockchainService;
  private tokenOperations: TokenOperations;
  private directPayment: DirectPayment;
  private signingService: SigningService;

  constructor(blockchainService: BlockchainService) {
    this.blockchainService = blockchainService;
    this.tokenOperations = new TokenOperations(blockchainService);
    this.directPayment = new DirectPayment(blockchainService);
    this.signingService = new SigningService(blockchainService);
  }

  /**
   * 准备代付gas的支付请求（仅签名，不发送）
   * @param to 接收方地址
   * @param amount 支付金额
   * @param seq 序列号，默认为当前时间戳
   * @param tokenAddress 代币地址，如不提供则为原生代币
   * @param deadline 过期时间戳（秒，bigint，必须是未来的绝对时间戳）
   * @returns 包含签名的中继请求数据
   */
  async preparePayment(
    to: string,
    amount: bigint,
    seq: bigint,
    tokenAddress: string,
    deadline: bigint
  ): Promise<RelayedRequestData> {
    await this.blockchainService.ensureWalletConnected();
    const account = this.blockchainService.getAccount();
    const chainId = this.blockchainService.getChainId();

    if (!account || chainId === undefined) {
      throw new Error('钱包未连接或初始化失败');
    }

    console.log('准备代付gas支付请求:');
    console.log('- 接收方:', to);
    console.log('- 金额:', amount.toString());
    console.log('- 序列号:', seq.toString());
    console.log('- 代币地址:', tokenAddress || 'ETH');
    console.log('- 过期时间戳:', deadline.toString());

    // 如果是代币支付，确保有足够的授权
    if (tokenAddress) {
      await this.tokenOperations.ensureTokenAllowance(
        tokenAddress,
        this.blockchainService.getPaymentContractAddress(),
        amount
      );
    }

    // 编码支付函数调用
    const payCallData = this.directPayment.encodePayCallData(
      tokenAddress || '0x0000000000000000000000000000000000000000',
      to,
      amount,
      seq
    );

    // 直接使用传入的绝对时间戳
    const deadlineValue = deadline;
    
    // 获取用户地址的 nonce
    const userNonce = await this.blockchainService.getNonce(account.address);
    
    // 动态估算 gas
    const gas = await this.blockchainService.getEstimateClient()!.estimateContractGas({
      address: this.blockchainService.getPaymentContractAddress() as `0x${string}`,
      abi: PAYMENT_CONTRACT_ABI,
      functionName: 'pay',
      args: [
        (tokenAddress || '0x0000000000000000000000000000000000000000') as `0x${string}`,
        to as `0x${string}`,
        amount,
        seq
      ],
      account: account.address
    });

    // 创建符合统一格式的请求数据
    const request: RelayedRequestData = {
      from: account.address,
      to: this.blockchainService.getPaymentContractAddress(),
      value: tokenAddress ? '0' : amount.toString(), // 如果是代币支付，value为0
      gas: gas.toString(), // 动态估算
      nonce: userNonce.toString(),
      deadline: deadlineValue.toString(),
      data: payCallData
    };

    console.log('构建的请求数据:', request);

    // 尝试生成签名
    try {
      const signature = await this.signingService.signTypedData(request, chainId, userNonce);
      request.signature = signature;
      console.log('签名成功');
    } catch (signError: any) {
      console.error('签名失败:', signError);
      console.warn('返回未签名的请求数据');
    }

    return request;
  }
} 