export interface DelegateConfig {
  paymentContractAddress?: string;
  vaultContractAddress?: string; // 金库合约地址
  forwarderAddress?: string; // 转发合约地址
  rpcUrl?:string;
}

export interface TransferParams {
  to: string;
  amount: bigint;
  tokenAddress?: string;
  gasFee?: bigint;
  seq?: bigint;
  contractAddress?: string;
  payer?: string;
}

export interface ProcessResult {
  request: {
    from: string;     // address
    to: string;       // address
    value: string;    // uint256
    gas: string;      // uint256
    nonce: string;    // uint256
    deadline: string; // uint48
    data: string;     // bytes
    signature?: string; // bytes - 签名
  };
}

export interface EncryptionResult {
  encryptedKey: string;
  encryptedData: string;
  iv: string;
}

export interface Call {
  data: `0x${string}`;
  to: string;
  value: bigint;
}

// 促销分档配置
export interface PromoTier {
  minAmount: bigint;       // 最低消费金额
  discountRate: bigint;    // 折扣率（百分制，100=无折扣）
  voucherAmount: bigint;   // 返券金额
  pointAmount: bigint;     // 返积分数量
}

// 操作类型枚举
export enum OperationType {
  Payment = 'payment',      // 支付操作
  Consume = 'consume',      // 消费操作
  Deposit = 'deposit',      // 存款操作
  Withdraw = 'withdraw'     // 提款操作
}

// 中继请求接口
export interface RelayerRequest {
  from: string;             // 发送方地址
  to: string;               // 接收方地址
  token: string;            // 代币地址
  amount: bigint;           // 金额
  seq: bigint;              // 序列号
  gasFee?: bigint;          // Gas费用(可选)
  consumerId?: string;      // 消费者ID(消费时需要)
  merchantId?: bigint;      // 商户ID（int64）
  voucherId?: bigint;       // 代金券ID(代金券消费时需要)
  pointToUse?: bigint;      // 使用的积分(代金券消费时需要)
  signature: string;        // 签名
  deadline: number;         // 截止时间
  data?: `0x${string}`;     // 调用数据
  value?: string;           // 原生代币值
  gas?: string;             // Gas限制
}

// 验证结果接口
export interface ValidationResult {
  success: boolean;         // 是否验证成功
  error?: string;           // 错误信息
  fee?: bigint;             // 手续费
  toAmount?: bigint;        // 接收方到账金额
  rewardAmount?: bigint;    // 奖励金额(消费时)
  merchantAmount?: bigint;  // 商家到账金额(代金券消费时)
  voucherReward?: bigint;   // 返券金额
  discountedAmount?: bigint; // 折扣后金额
  pointsUsed?: bigint;      // 使用的积分
  voucherUsed?: bigint;     // 使用的代金券金额
  spendAmount?: bigint;     // 实际扣除金额
}

// 商户管理员操作结果
export interface MerchantOperationResult {
  success: boolean;         // 操作是否成功
  message: string;          // 结果消息
  data?: any;               // 返回数据
} 