import { type Hash, encodeFunctionData } from 'viem';
import { PAYMENT_CONTRACT_ABI } from '../../config/contract';
import { BlockchainService } from '../blockchain';
import { TokenOperations } from './token';

/**
 * 直接支付类
 * 负责处理用户付gas的直接支付操作
 */
export class DirectPayment {
  private blockchainService: BlockchainService;
  private tokenOperations: TokenOperations;

  constructor(blockchainService: BlockchainService) {
    this.blockchainService = blockchainService;
    this.tokenOperations = new TokenOperations(blockchainService);
  }

  /**
   * 用户直接支付（自己付gas）
   * @param to 接收方地址
   * @param amount 支付金额
   * @param tokenAddress 代币地址，如不提供则为原生代币(ETH)
   * @param seq 序列号，默认为当前时间戳
   * @returns 交易哈希
   */
  async pay(
    to: string,
    amount: bigint,
    tokenAddress?: string,
    seq: bigint = BigInt(Math.floor(Date.now() / 1000))
  ): Promise<Hash> {
    await this.blockchainService.ensureWalletConnected();
    const wallet = this.blockchainService.getWalletClient();
    const account = this.blockchainService.getAccount();
    const chainId = this.blockchainService.getChainId();

    if (!wallet || !account || chainId === undefined) {
      throw new Error('钱包未连接或初始化失败');
    }

    console.log('开始直接支付:');
    console.log('- 接收方:', to);
    console.log('- 金额:', amount.toString());
    console.log('- 代币地址:', tokenAddress || 'ETH');
    console.log('- 序列号:', seq.toString());
    console.log('- paymentContractAddress:', this.blockchainService.getPaymentContractAddress());

    // 如果是代币支付，确保有足够的授权
    if (tokenAddress) {
      await this.tokenOperations.ensureTokenAllowance(
        tokenAddress,
        this.blockchainService.getPaymentContractAddress(),
        amount
      );
    } 
    

    // 执行支付交易
    const hash = await wallet.writeContract({
      account,
      chain: { id: chainId } as any,
      address: this.blockchainService.getPaymentContractAddress() as `0x${string}`,
      abi: PAYMENT_CONTRACT_ABI,
      functionName: 'pay',
      args: [
        (tokenAddress || '0x0000000000000000000000000000000000000000') as `0x${string}`,
        to as `0x${string}`,
        amount,
        seq
      ],
      value: tokenAddress ? 0n : amount // 如果是原生代币，设置value
    });

    console.log('支付交易已发送，哈希:', hash);
    return hash;
  }

  /**
   * 编码支付函数调用数据
   * @param token 代币地址
   * @param to 接收方地址
   * @param amount 金额
   * @param seq 序列号
   * @returns 编码后的调用数据
   */
  encodePayCallData(
    token: string,
    to: string,
    amount: bigint,
    seq: bigint = 0n
  ): `0x${string}` {
    const payData = encodeFunctionData({
      abi: PAYMENT_CONTRACT_ABI,
      functionName: 'pay',
      args: [
        token as `0x${string}`,
        to as `0x${string}`,
        amount,
        seq
      ]
    }) as `0x${string}`;
    
    console.log('编码支付调用参数:');
    console.log('- token:', token);
    console.log('- to:', to);
    console.log('- amount:', amount.toString());
    console.log('- seq:', seq.toString());

    return payData;
  }
} 