import { 
  encodeFunctionData,
  keccak256,
  type Hash,
  type Account
} from 'viem';
import { 
  ERC20_ABI, 
  PAYMENT_CONTRACT_ABI, 
  forwarderAddress,  
  FORWARD_REQUEST_TYPES,
  EIP712_DOMAIN_TYPE
} from '../config/contract';
import { BlockchainService } from './blockchain';
import { SigningService } from './signing';
import { TokenOperations, DirectPayment, VaultOperations, RelayedPayment, type RelayedRequestData } from './operations';

// 检查是否在浏览器环境中
const isBrowser = typeof window !== 'undefined';

// 安全地访问window.ethereum
const getEthereum = () => {
  if (!isBrowser) {
    return null;
  }
  return window.ethereum;
};

export interface ForwardRequestData {
  from: string;     // address
  to: string;       // address
  value: string;    // uint256
  gas: string;      // uint256
  nonce: string;    // uint256
  deadline: string; // uint48
  data: string;     // bytes
}

export interface SignedRequest extends ForwardRequestData {
  signature?: string;
}

export class TransactionService {
  private blockchainService: BlockchainService;
  private signingService: SigningService;
  private tokenOperations: TokenOperations;
  private directPayment: DirectPayment;
  private vaultOperations: VaultOperations;
  private relayedPayment: RelayedPayment;

  constructor(blockchainService: BlockchainService) {
    this.blockchainService = blockchainService;
    this.signingService = new SigningService(blockchainService);
    this.tokenOperations = new TokenOperations(blockchainService);
    this.directPayment = new DirectPayment(blockchainService);
    this.vaultOperations = new VaultOperations(blockchainService);
    this.relayedPayment = new RelayedPayment(blockchainService);
  }

  // === ERC20代币相关 ===

  // 授权代币
  async approveToken(tokenAddress: string, spender: string, amount: bigint): Promise<Hash> {
    return this.tokenOperations.approveToken(tokenAddress, spender, amount);
  }

  // 检查授权额度
  async checkAllowance(tokenAddress: string, owner: string, spender: string): Promise<bigint> {
    return this.tokenOperations.checkAllowance(tokenAddress, owner, spender);
  }

  // === 支付合约相关 ===

  // 编码对 pay 函数的调用
  encodePayCallData(
    token: string,
    to: string,
    amount: bigint, // 金额
    seq: bigint = 0n,  // api获取的
  ): `0x${string}` {
    return this.directPayment.encodePayCallData(token, to, amount, seq);
  }

  // 用户直接支付（自己付gas）
  async userPayDirect(
    to: string,
    amount: bigint,
    tokenAddress?: string,
    seq: bigint = BigInt(Math.floor(Date.now() / 1000))
  ): Promise<Hash> {
    return this.directPayment.pay(to, amount, tokenAddress, seq);
  }

  // 准备代付gas的支付请求 (仅签名，不发送)
  async prepareRelayedPayment(
    to: string,
    amount: bigint,
    seq: bigint,
    tokenAddress: string,
    deadline: bigint
  ): Promise<RelayedRequestData> {
    return this.relayedPayment.preparePayment(to, amount, seq, tokenAddress, deadline);
  }

  // === UnifiedVault相关 ===

  // 存款到金库
  async depositToVault(merchantId: bigint, tokenAddress: string, amount: bigint): Promise<Hash> {
    return this.vaultOperations.deposit(merchantId, tokenAddress, amount);
  }

  // 从金库提款
  async withdrawFromVault(merchantId: bigint, tokenAddress: string, amount: bigint): Promise<Hash> {
    return this.vaultOperations.withdraw(merchantId, tokenAddress, amount);
  }

  // 从金库消费
  async consumeFromVault(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    voucherId: bigint = 0n,
    pointToUse: bigint = 0n,
    idx: bigint,
    seq: bigint,
    recipient: string,
    userAddress: string
  ): Promise<Hash> {
    return this.vaultOperations.consumeDirect(merchantId, tokenAddress, amount, voucherId, pointToUse, seq, idx, recipient, userAddress);
  }

  async getUserBalance(userAddress: string, merchantId: bigint, tokenAddress: string): Promise<bigint> {
    return this.vaultOperations.getUserBalance(userAddress, merchantId, tokenAddress);
  }

  async getUserPoints(userAddress: string, merchantId: bigint): Promise<bigint> {
    return this.vaultOperations.getUserPoints(userAddress, merchantId);
  }

  async getSpecificUserPoints(userAddress: string, merchantId: bigint): Promise<bigint> {
    return this.vaultOperations.getUserPoints(userAddress, merchantId);
  }

  // 准备代付gas的消费请求
  async prepareRelayedConsume(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    voucherId: bigint = 0n,
    pointToUse: bigint = 0n,
    idx: bigint,
    seq: bigint,
    recipient: string,
    deadline: bigint,
    userAddress: string
  ): Promise<RelayedRequestData> {
    return this.vaultOperations.prepareRelayedConsume(merchantId, tokenAddress, amount, voucherId, pointToUse, seq, idx, recipient, deadline, userAddress);
  }

  // 准备代付gas的存款请求
  async prepareRelayedDeposit(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    deadline: bigint
  ): Promise<RelayedRequestData> {
    return this.vaultOperations.prepareRelayedDeposit(merchantId, tokenAddress, amount, deadline);
  }

  // 准备代付gas的提款请求
  async prepareRelayedWithdraw(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    deadline: bigint
  ): Promise<RelayedRequestData> {
    return this.vaultOperations.prepareRelayedWithdraw(merchantId, tokenAddress, amount, deadline);
  }
} 