import { BlockchainService, BlockchainConfig } from './blockchain';
import { TokenOperations } from './operations/token';
import { DirectPayment } from './operations/direct-payment';
import { RelayedPayment, RelayedRequestData } from './operations/relayed-payment';
import { VaultOperations } from './operations/vault';
import type { Hash } from 'viem';
import { MerchantConfigManager } from './merchant-config-manager';
import { TransactionService } from './transaction';

/**
 * Web3Delegate主类
 * 提供统一的接口来访问所有区块链操作功能
 */
export class Web3Delegate {
  private blockchainService: BlockchainService;
  
  // 各功能模块
  public readonly token: TokenOperations;
  public readonly directPayment: DirectPayment;
  public readonly relayedPayment: RelayedPayment;
  public readonly vault: VaultOperations;
  public readonly merchantConfigManager: MerchantConfigManager;
  public readonly transactionService: TransactionService;

  constructor(config: BlockchainConfig = {  }) {
    console.log('[Web3Delegate] 构造收到的config:', config);
    // 初始化基础服务
    this.blockchainService = new BlockchainService(config);
    console.log('[Web3Delegate] blockchainService.getPaymentContractAddress():', this.blockchainService.getPaymentContractAddress());
    // 初始化各功能模块
    this.token = new TokenOperations(this.blockchainService);
    this.directPayment = new DirectPayment(this.blockchainService);
    this.relayedPayment = new RelayedPayment(this.blockchainService);
    this.vault = new VaultOperations(this.blockchainService);
    this.merchantConfigManager = new MerchantConfigManager(config);
    this.transactionService = new TransactionService(this.blockchainService);
  }

  // === 基础钱包操作 ===

  /**
   * 连接钱包
   * @returns 连接的钱包地址
   */
  async connectWallet(): Promise<string> {
    return this.blockchainService.connectWallet();
  }

  /**
   * 检查钱包是否已连接
   * @returns 是否已连接
   */
  isConnected(): boolean {
    return this.blockchainService.isConnected();
  }

  /**
   * 获取当前连接的钱包地址
   * @returns 当前连接的钱包地址
   */
  getConnectedAddress(): string | undefined {
    return this.blockchainService.getConnectedAddress();
  }

  /**
   * 获取当前连接的链ID
   * @returns 当前连接的链ID
   */
  getChainId(): number | undefined {
    return this.blockchainService.getChainId();
  }

  // === 向后兼容的快捷方法 ===

  /**
   * ERC20代币授权
   * @param tokenAddress 代币地址
   * @param spender 被授权者地址
   * @param amount 授权金额
   * @returns 交易哈希
   */
  async approveToken(tokenAddress: string, spender: string, amount: bigint): Promise<Hash> {
    return this.token.approveToken(tokenAddress, spender, amount);
  }

  /**
   * 检查代币授权额度
   * @param tokenAddress 代币地址
   * @param owner 代币持有者
   * @param spender 被授权者
   * @returns 授权额度
   */
  async checkAllowance(tokenAddress: string, owner: string, spender: string): Promise<bigint> {
    return this.token.checkAllowance(tokenAddress, owner, spender);
  }

  /**
   * 确保代币授权额度充足
   * @param tokenAddress 代币地址
   * @param spender 被授权者
   * @param amount 需要的授权额度
   * @returns 是否授权成功
   */
  async ensureTokenAllowance(tokenAddress: string, spender: string, amount: bigint): Promise<boolean> {
    return this.token.ensureTokenAllowance(tokenAddress, spender, amount);
  }

  /**
   * 用户直接支付（自己付gas）
   * @param to 接收方地址
   * @param amount 支付金额
   * @param tokenAddress 代币地址，如不提供则为原生代币
   * @param seq 序列号，默认为当前时间戳
   * @returns 交易哈希
   */
  async userPayDirect(to: string, amount: bigint, tokenAddress?: string, seq?: bigint): Promise<Hash> {
    return this.directPayment.pay(to, amount, tokenAddress, seq);
  }

  /**
   * 准备代付gas的支付请求（仅签名，不发送）
   * @param to 接收方地址
   * @param amount 支付金额
   * @param seq 序列号，默认为当前时间戳
   * @param tokenAddress 代币地址，如不提供则为原生代币
   * @param deadline 过期时间戳（秒，bigint，必须是未来的绝对时间戳）
   * @returns 包含签名的请求数据
   */
  async prepareRelayedPayment(
    to: string, 
    amount: bigint, 
    seq: bigint, 
    tokenAddress: string, 
    deadline: bigint
  ): Promise<RelayedRequestData> {
    return this.transactionService.prepareRelayedPayment(to, amount, seq, tokenAddress, deadline);
  }

  /**
   * 向金库存款（用户付gas）
   * @param merchantId 商户ID
   * @param tokenAddress 代币地址
   * @param amount 存款金额
   * @returns 交易哈希
   */
  async depositToVault(merchantId: bigint, tokenAddress: string, amount: bigint): Promise<Hash> {
    return this.vault.deposit(merchantId, tokenAddress, amount);
  }

  /**
   * 从金库提现（用户付gas）
   * @param merchantId 商户ID
   * @param tokenAddress 代币地址
   * @param amount 提现金额
   * @returns 交易哈希
   */
  async withdrawFromVault(merchantId: bigint, tokenAddress: string, amount: bigint): Promise<Hash> {
    return this.vault.withdraw(merchantId, tokenAddress, amount);
  }

  /**
   * 查询金库余额
   * @param merchantId 商户ID
   * @param tokenAddress 代币地址
   * @returns 余额
   */
  async getUserBalance(userAddress: string, merchantId: bigint, tokenAddress: string): Promise<bigint> {
    return this.vault.getUserBalance(userAddress, merchantId, tokenAddress);
  }

  /**
   * 用户直接从金库消费（自己付gas）
   * @param merchantId 商家ID
   * @param tokenAddress 代币地址
   * @param amount 消费金额
   * @param voucherId 代金券ID
   * @param pointToUse 使用的积分
   * @param idx 由前端传入
   * @param seq 序列号
   * @param recipient 接收方地址
   * @param userAddress 用户地址
   * @returns 交易哈希
   */
  async consumeFromVault(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    voucherId: bigint = 0n,
    pointToUse: bigint = 0n,
    idx: bigint,
    seq: bigint,
    recipient: string,
    userAddress: string
  ): Promise<Hash> {
    if (!recipient) throw new Error('recipient is required');
    return this.vault.consumeDirect(
      merchantId,
      tokenAddress,
      amount,
      voucherId,
      pointToUse,
      seq,
      idx,
      recipient,
      userAddress
    );
  }

  /**
   * 准备代付gas的金库消费请求（仅签名，不发送）
   * @param merchantId 商家ID
   * @param tokenAddress 代币地址
   * @param amount 消费金额
   * @param voucherId 代金券ID
   * @param pointToUse 使用的积分
   * @param idx 由前端传入
   * @param seq 序列号
   * @param recipient 接收方地址
   * @param deadline 过期时间戳（秒，bigint，必须是未来的绝对时间戳）
   * @param userAddress 用户地址
   * @returns 包含签名的请求数据
   */
  async prepareRelayedConsume(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    voucherId: bigint = 0n,
    pointToUse: bigint = 0n,
    idx: bigint,
    seq: bigint,
    recipient: string,
    deadline: bigint,
    userAddress: string
  ): Promise<RelayedRequestData> {
    if (!recipient) throw new Error('recipient is required');
    if (!deadline) throw new Error('deadline is required');
    return this.transactionService.prepareRelayedConsume(
      merchantId,
      tokenAddress,
      amount,
      voucherId,
      pointToUse,
      idx,
      seq,
      recipient,
      deadline,
      userAddress
    );
  }

  /**
   * 准备代付gas的金库存款请求（仅签名，不发送）
   * @param merchantId 商户ID
   * @param tokenAddress 代币地址
   * @param amount 存款金额
   * @param deadline 过期时间戳（秒，bigint，必须是未来的绝对时间戳）
   * @returns 包含签名的请求数据
   */
  async prepareRelayedDeposit(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    deadline: bigint
  ): Promise<RelayedRequestData> {
    return this.transactionService.prepareRelayedDeposit(merchantId, tokenAddress, amount, deadline);
  }

  /**
   * 准备代付gas的金库提现请求（仅签名，不发送）
   * @param merchantId 商户ID
   * @param tokenAddress 代币地址
   * @param amount 提现金额
   * @param deadline 过期时间戳（秒，bigint，必须是未来的绝对时间戳）
   * @returns 包含签名的请求数据
   */
  async prepareRelayedWithdraw(
    merchantId: bigint,
    tokenAddress: string,
    amount: bigint,
    deadline: bigint
  ): Promise<RelayedRequestData> {
    return this.transactionService.prepareRelayedWithdraw(merchantId, tokenAddress, amount, deadline);
  }
}

// 导出所有类型和类
export type { RelayedRequestData } from './operations/relayed-payment';
export type { ForwardRequestData } from './signing';
export type { BlockchainConfig } from './blockchain';

export { BlockchainService } from './blockchain';
export { TokenOperations } from './operations/token';
export { DirectPayment } from './operations/direct-payment';
export { RelayedPayment } from './operations/relayed-payment';
export { VaultOperations } from './operations/vault';
export { SigningService } from './signing';

// 默认导出
export default Web3Delegate; 