import { 
  type Hash,
  type Address,
  createPublicClient,
  http
} from 'viem';
import { ERC20_ABI, forwarderAddress, paymentContractAddress, VaultContractAddress, UNIFIED_VAULT_ABI, ERC2771_FORWARDER_ABI } from '../config/contract';
import { WalletService } from './wallet-service';
import { DelegateConfig } from '../types';

export interface BlockchainConfig {
  rpcUrl?: string; // 添加自定义 RPC URL 配置
  forwarderAddress?: string;
  paymentContractAddress?: string;
  vaultContractAddress?: string;
}

export class BlockchainService extends WalletService {
  private customRpcClient: any; // 自定义 RPC 客户端

  constructor(config: BlockchainConfig = { }) {
    console.log('[BlockchainService] 构造收到的config:', config);
    // 转换为 DelegateConfig - 不强制指定 RPC URL
    const delegateConfig: DelegateConfig = {
      rpcUrl: config.rpcUrl,
      paymentContractAddress: config.paymentContractAddress || paymentContractAddress,
      vaultContractAddress: config.vaultContractAddress || VaultContractAddress,
      forwarderAddress: config.forwarderAddress || forwarderAddress,
    };
    console.log('[BlockchainService] delegateConfig:', delegateConfig);
    super(delegateConfig);
    this.customRpcClient = null;
  }

  // 获取钱包客户端
  getWalletClient() {
    return this.wallet;
  }

  // 获取账户
  getAccount() {
    return this.account;
  }

  // 检查钱包状态
  async ensureWalletConnected(timeoutMs: number = 15000): Promise<void> {
    await this.ensureConnected(timeoutMs);
  }

  // 检查并确保代币授权
  async ensureTokenAllowance(tokenAddress: string, spender: string, amount: bigint): Promise<boolean> {
    await this.ensureWalletConnected();

    // 查询当前allowance - 使用自定义 RPC 客户端
    const currentAllowance: bigint = await this.readContract<bigint>(
      tokenAddress,
      ERC20_ABI,
      'allowance',
      [this.account!.address as `0x${string}`, spender as `0x${string}`]
    );

    console.log(`当前allowance: ${currentAllowance} 需要: ${amount}`);
    if (currentAllowance >= amount) {
      console.log('授权额度充足');
      return true; // 不需要approve
    }

    // 额度不足，自动请求用户 approve
    console.log('授权额度不足，发起approve...');

    const approveHash = await this.writeContract(
      tokenAddress,
      ERC20_ABI,
      'approve',
      [spender as `0x${string}`, amount]
    );

    console.log('approve成功');
    return true;
  }

  // 获取Nonce
  async getNonce(address: string, options?: { timeout?: number }): Promise<bigint> {
    try {
      const nonce = await this.readContract<bigint>(
        this.getForwarderAddress(),
        ERC2771_FORWARDER_ABI,
        'nonces',
        [address as `0x${string}`],
        options
      );
      
      return nonce;
    } catch (error) {
      // 这里不吞掉错误，直接抛出
      throw error;
    }
  }
  
  // 发送交易
  async sendTransaction(to: string, value: bigint, data?: `0x${string}`): Promise<Hash> {
    await this.ensureWalletConnected();
    
    console.log('发送交易...');
    console.log('- 发送方:', this.account!.address);
    console.log('- 接收方:', to);
    console.log('- 金额:', value.toString());
    
    // 构建交易对象
    const transaction = {
      account: this.account!,
      chain: this.chainId ? { id: this.chainId } as any : undefined,
      to: to as `0x${string}`,
      value,
      data
    };
    
    try {
      // 发送交易
      const hash = await this.wallet!.sendTransaction(transaction);
      console.log('交易已发送，等待确认，哈希:', hash);
      
      // 等待交易确认
      const receipt = await this.publicClient!.waitForTransactionReceipt({ hash });
      
      if (receipt.status !== 'success') {
        throw new Error('交易执行失败');
      }
      
      console.log('交易已确认');
      return hash;
    } catch (error: any) {
      console.error('发送交易失败:', error);
      throw new Error(`发送交易失败: ${error.message}`);
    }
  }
  
  // 调用合约写方法
  async writeContract(
    address: string, 
    abi: any, 
    functionName: string, 
    args: any[],
    value: bigint = 0n
  ): Promise<Hash> {
    await this.ensureWalletConnected();
    
    console.log('调用合约写方法...');
    console.log('- 合约地址:', address);
    console.log('- 函数名:', functionName);
    console.log('- 参数:', args);
    
    try {
      // 调用合约
      const hash = await this.wallet!.writeContract({
        account: this.account!,
        chain: this.chainId ? { id: this.chainId } as any : undefined,
        address: address as `0x${string}`,
        abi,
        functionName,
        args,
        value
      });
      
      console.log('合约调用已发送，等待确认，哈希:', hash);
      
      // 等待交易确认
      const receipt = await this.publicClient!.waitForTransactionReceipt({ hash });
      
      if (receipt.status !== 'success') {
        throw new Error('合约调用执行失败');
      }
      
      console.log('合约调用已确认');
      return hash;
    } catch (error: any) {
      console.error('调用合约写方法失败:', error);
      throw new Error(`调用合约写方法失败: ${error.message}`);
    }
  }
  
  // 调用合约读方法 - 使用自定义 RPC 客户端而不是钱包
  async readContract<T = any>(
    address: string,
    abi: any,
    functionName: string,
    args: any[] = [],
    options?: { timeout?: number }
  ): Promise<T> {
    // 不需要确保钱包已初始化，直接使用自定义 RPC 客户端
    const timeout = options?.timeout ?? 10000; // 默认10秒
    
    console.log('调用合约读方法...');
    console.log('- 合约地址:', address);
    console.log('- 函数名:', functionName);
    console.log('- 参数:', args);
    
    try {
      // 带超时的合约调用 - 获取或创建自定义 RPC 客户端
      const result = await Promise.race([
        this.getCustomRpcClient().readContract({
          address: address as `0x${string}`,
          abi,
          functionName,
          args
        }) as Promise<T>,
        new Promise<never>((_, reject) => 
          setTimeout(() => reject(new Error('TimeoutError: readContract timeout')), timeout)
        )
      ]);
      
      console.log('合约读取结果:', result);
      return result;
    } catch (error: any) {
      console.error('调用合约读方法失败:', error);
      throw new Error(`调用合约读方法失败: ${error.message}`);
    }
  }

  getPaymentContractAddress(): string {
    const addr = this.config?.paymentContractAddress || paymentContractAddress;
    console.log('[BlockchainService] getPaymentContractAddress 返回:', addr);
    return addr;
  }

  getVaultContractAddress(): string {
    const addr = this.config?.vaultContractAddress || VaultContractAddress;
    console.log('[BlockchainService] getVaultContractAddress 返回:', addr);
    return addr;
  }

  getForwarderAddress(): string {
    const addr = this.config?.forwarderAddress || forwarderAddress;
    console.log('[BlockchainService] getForwarderAddress 返回:', addr);
    return addr;
  }

  /**
   * 获取或创建自定义 RPC 客户端
   * @returns 自定义 RPC 客户端
   */
  private getCustomRpcClient() {
    // 如果已经创建，直接返回
    if (this.customRpcClient) {
      return this.customRpcClient;
    }
    
    // 尝试使用配置的 RPC URL
    const rpcUrl = this.config.rpcUrl;
    
    if (rpcUrl) {
      console.log('使用配置的 RPC URL:', rpcUrl);
      this.customRpcClient = createPublicClient({
        transport: http(rpcUrl)
      });
      return this.customRpcClient;
    }
    
    // 如果已经有 publicClient (通过钱包连接)，优先使用它
    if (this.publicClient) {
      console.log('使用钱包提供的 RPC 连接');
      this.customRpcClient = this.publicClient;
      return this.customRpcClient;
    }
    
    // 如果没有配置RPC URL且没有钱包连接，抛出错误
    console.error('未提供RPC URL且未连接钱包');
    throw new Error('未提供RPC URL且未连接钱包，请配置RPC URL或连接MetaMask');
  }

  /**
   * 获取可用于 estimateContractGas 的客户端
   */
  public getEstimateClient() {
    // 优先用 publicClient
    if (this.publicClient) return this.publicClient;
    return this.getCustomRpcClient();
  }
} 