import { 
  type Account,
  type Address,
  stringToHex,
  pad
} from 'viem';
import { DelegateConfig, MerchantOperationResult, PromoTier } from '../types';
import { UNIFIED_VAULT_ABI } from '../config/contract';
import { BlockchainService } from './blockchain';

export class MerchantConfigManager extends BlockchainService {

  constructor(config: DelegateConfig) {
    super(config);
  }

  /**
   * 设置或更新商户活动档位（PromoTier）
   * @param merchantId 商户ID
   * @param idx 活动档位编号（由前端维护，0表示新建，非0表示更新）
   * @param minAmount 最低消费金额
   * @param discountRate 折扣率（百分制，100=无折扣）
   * @param voucherAmount 返券金额
   * @param pointAmount 返积分数量
   * @param startTime 档位开始时间
   * @param endTime 档位结束时间
   * @param voucherExpirePeriod 券奖励有效期（秒）
   * @param enabled 档位开关
   * @returns 交易哈希
   */
  async setPromoTier(
    merchantId: bigint,
    idx: number,
    minAmount: bigint,
    discountRate: bigint,
    voucherAmount: bigint,
    pointAmount: bigint,
    startTime: bigint,
    endTime: bigint,
    voucherExpirePeriod: bigint,
    enabled: boolean
  ): Promise<string> {
    await this.ensureWalletConnected();
    const merchantIdBytes32 = this.int64ToBytes32(merchantId);
    const txHash = await this.writeContract(
      this.getVaultContractAddress(),
      UNIFIED_VAULT_ABI,
      'setPromoTier',
      [
        merchantIdBytes32,
        idx,
        minAmount,
        discountRate,
        voucherAmount,
        pointAmount,
        startTime,
        endTime,
        voucherExpirePeriod,
        enabled
      ]
    );
    return txHash;
  }

  /**
   * int64 转 bytes32（高位补零，右对齐）
   */
  private int64ToBytes32(id: bigint | number): `0x${string}` {
    const hex = BigInt(id).toString(16);
    return ('0x' + hex.padStart(64, '0')) as `0x${string}`;
  }

  /**
   * 生成商户ID
   * @param merchantName 商户名称或标识符
   * @returns 32字节的商户ID
   */
  generateMerchantId(merchantName: string): string {
    if (!merchantName || merchantName.trim() === '') {
      throw new Error('商户名称不能为空');
    }
    
    // 将字符串转换为十六进制
    const merchantIdHex = stringToHex(merchantName);
    
    // 确保是bytes32（32字节，64个十六进制字符加上0x前缀）
    const merchantId = pad(merchantIdHex, { size: 32 });
    
    console.log('生成的merchantId:', merchantId);
    return merchantId;
  }

  /**
   * 设置商户操作员
   * @param merchantId 商户ID
   * @param operator 操作员地址
   * @returns 操作结果
   */
  async setMerchantOperator(
    merchantId: bigint,
    operator: string
  ): Promise<MerchantOperationResult> {
    try {
      await this.ensureWalletConnected();

      console.log('设置商户操作员...');
      console.log('- 商户ID:', merchantId);
      console.log('- 操作员地址:', operator);
      
      const merchantIdBytes32 = this.int64ToBytes32(merchantId);
      
      const txHash = await this.writeContract(
        this.getVaultContractAddress(),
        UNIFIED_VAULT_ABI,
        'setMerchantOperator',
        [merchantIdBytes32, operator as `0x${string}`]
      );
      
      console.log('交易已发送，哈希:', txHash);
      
      return {
        success: true,
        message: '设置商户操作员交易已发送',
        data: { txHash }
      };
    } catch (error: any) {
      console.error('设置商户操作员失败:', error);
      return {
        success: false,
        message: `设置商户操作员失败: ${error.message}`
      };
    }
  }

  /**
   * 检查商户操作员权限
   * @param merchantId 商户ID
   * @param operator 操作员地址
   * @returns 权限状态
   */
  async checkMerchantOperator(merchantId: bigint, operator: string): Promise<MerchantOperationResult> {
    try {
      await this.ensureWalletConnected();

      console.log('检查商户操作员权限...');
      console.log('- 商户ID:', merchantId);
      console.log('- 操作员地址:', operator);
      
      const merchantIdBytes32 = this.int64ToBytes32(merchantId);
      
      const operatorAddress = await this.readContract<string>(
        this.getVaultContractAddress(),
        UNIFIED_VAULT_ABI,
        'merchantOperator',
        [merchantIdBytes32]
      );
      const hasPermission = operatorAddress.toLowerCase() === operator.toLowerCase();
      
      console.log('操作员权限状态:', hasPermission);
      
      return {
        success: true,
        message: '获取商户操作员权限成功',
        data: { hasPermission, operatorAddress }
      };
    } catch (error: any) {
      console.error('获取商户操作员权限失败:', error);
      return {
        success: false,
        message: `获取商户操作员权限失败: ${error.message}`
      };
    }
  }
}

  