import { recoverTypedDataAddress } from 'viem';
import { 
  FORWARD_REQUEST_TYPES,
  EIP712_DOMAIN_TYPE
} from '../config/contract';
import { BlockchainService } from './blockchain';

// 检查是否在浏览器环境中
const isBrowser = typeof window !== 'undefined';

export interface ForwardRequestData {
  from: string;
  to: string;
  value: string;
  gas: string;
  nonce: string;
  deadline: string;
  data: string;
}

/**
 * 签名服务类
 * 负责处理EIP-712签名和验证
 */
export class SigningService {
  private blockchainService: BlockchainService;

  constructor(blockchainService: BlockchainService) {
    this.blockchainService = blockchainService;
  }

  /**
   * 验证EIP-712签名
   */
  async verifyTypedDataSignature({
    chainId,
    message,
    signature,
    expectedSigner
  }: {
    chainId: number,
    message: any
    signature: string
    expectedSigner: string
  }) {
    const domain = {
      name: 'MetaTxForwarder',
      version: '1',
      chainId: chainId,
      verifyingContract: this.blockchainService.getForwarderAddress() as `0x${string}`
    } as const;
    
    const signer = await recoverTypedDataAddress({
      domain,
      types: FORWARD_REQUEST_TYPES,
      primaryType: 'ForwardRequest',
      message,
      signature: signature as `0x${string}`
    })
    
    const valid = signer.toLowerCase() === expectedSigner.toLowerCase()
    return { valid, signer }
  }

  /**
   * 生成 EIP-712 签名
   */
  async signTypedData(
    request: ForwardRequestData,
    chainId: number,
    nonce: bigint
  ): Promise<string> {
    console.log('开始签名过程，参数:');
    console.log('- 账户地址:', request.from);
    console.log('- 链 ID:', chainId);
    console.log('- Nonce:', nonce.toString());

    const domain = {
      name: 'MetaTxForwarder',
      version: '1',
      chainId: chainId,
      verifyingContract: this.blockchainService.getForwarderAddress() as `0x${string}`
    } as const;
    
    // 确保所有值都是字符串形式，避免 BigInt 序列化问题
    const safeRequest = {
      from: request.from,
      to: request.to,
      value: request.value.toString(),
      gas: request.gas.toString(),
      nonce: request.nonce.toString(),
      deadline: request.deadline.toString(),
      data: request.data
    };
    
    try {
      // 获取钱包客户端
      const wallet = this.blockchainService.getWalletClient();
      const account = this.blockchainService.getAccount();
      
      if (!wallet || !account) {
        throw new Error('钱包未连接或初始化失败');
      }
      
      // 尝试直接使用 viem 的钱包客户端进行签名
      if (wallet.signTypedData) {
        console.log('使用 viem 钱包客户端签名...');
        try {
          const signature = await wallet.signTypedData({
            account,
            domain,
            types: { ...FORWARD_REQUEST_TYPES },
            primaryType: 'ForwardRequest',
            message: safeRequest
          });
          console.log('viem 签名成功:', signature);
          return signature as string;
        } catch (viemError: any) {
          console.warn('viem 签名失败，回退到原生方法:', viemError.message);
        }
      }
      
      // 回退到使用浏览器的原生签名方法
      if (!isBrowser || !window.ethereum) {
        throw new Error('浏览器环境不可用或未安装钱包');
      }
      
      console.log('使用浏览器原生签名方法...');
      
      // 为钱包构建签名数据
      const typedDataForWallet = {
        domain: { ...domain, chainId: domain.chainId.toString() },
        message: safeRequest,
        primaryType: 'ForwardRequest',
        types: {
          EIP712Domain: EIP712_DOMAIN_TYPE,
          ...FORWARD_REQUEST_TYPES
        },
      };
      
      let signature: string;
      
      // 尝试 eth_signTypedData_v4，如果不支持则回退到其他方法
      try {
        signature = await window.ethereum.request({
          method: 'eth_signTypedData_v4',
          params: [
            request.from,
            JSON.stringify(typedDataForWallet)
          ]
        });
      } catch (v4Error: any) {
        console.warn('eth_signTypedData_v4 失败，尝试 eth_signTypedData_v3');
        signature = await window.ethereum.request({
          method: 'eth_signTypedData_v3',
          params: [
            request.from,
            JSON.stringify(typedDataForWallet)
          ]
        });
      }
      
      // 验证签名
      const { valid, signer } = await this.verifyTypedDataSignature({
        chainId,
        message: safeRequest,
        signature: signature,
        expectedSigner: request.from
      });
      
      console.log('签名验证：', valid, signer);
      
      if (!valid) {
        throw new Error('签名验证失败');
      }
      
      console.log('签名成功:', signature);
      return signature;
    } catch (error: any) {
      console.error('签名过程中出错:', error);
      throw new Error(`签名失败: ${error.message}`);
    }
  }
} 