import { 
  createWalletClient, 
  createPublicClient, 
  http,
  custom,
  type WalletClient,
  type PublicClient,
  type Account,
  type Address
} from 'viem';
import { DelegateConfig } from '../types';

// 检查是否在浏览器环境中
const isBrowser = typeof window !== 'undefined';

// 安全地访问window.ethereum
const getEthereum = () => {
  if (!isBrowser) {
    return null;
  }
  return window.ethereum;
};

// 检查是否有任何可用的钱包提供者
const getAnyWalletProvider = () => {
  if (!isBrowser) {
    return null;
  }
  
  // 检查各种钱包提供者
  return window.ethereum || 
         window.walletLinkExtension || 
         window.walletConnect || 
         window.coinbaseWalletExtension ||
         null;
};

export class WalletService {
  protected wallet?: WalletClient;
  protected publicClient?: PublicClient;
  protected config: DelegateConfig;
  protected account?: Account;
  protected chainId?: number;

  constructor(config: DelegateConfig) {
    this.config = config;
  }

  /**
   * 初始化客户端
   * @returns 是否初始化成功
   */
  protected async initializeClients(timeoutMs: number = 15000): Promise<boolean> {
    console.log('开始初始化钱包客户端...');
    
    // 检查是否在浏览器环境
    if (!isBrowser) {
      throw new Error('钱包只能在浏览器环境中使用');
    }

    // 尝试获取任何可用的钱包提供者
    const provider = getAnyWalletProvider();
    if (!provider) {
      console.error('未检测到可用的钱包');
      throw new Error('未检测到可用的钱包。请安装MetaMask、Coinbase Wallet或其他以太坊钱包。');
    }
  
    console.log('检测到钱包提供者，创建钱包客户端...');
    
    try {
      // 添加超时处理
      const withTimeout = <T>(promise: Promise<T>, ms: number, errorMessage: string): Promise<T> => {
        return Promise.race([
          promise,
          new Promise<T>((_, reject) => 
            setTimeout(() => reject(new Error(errorMessage)), ms)
          )
        ]);
      };

      // 获取当前链ID
      const chainIdHex = await withTimeout(
        provider.request({ method: 'eth_chainId' }),
        timeoutMs,
        '获取链ID超时，请检查钱包连接状态'
      );
      this.chainId = parseInt(chainIdHex as string, 16);
      console.log('从钱包获取到链 ID:', this.chainId);
      
      // 使用检测到的钱包 - 直接使用钱包提供者，不指定 RPC URL
      this.wallet = createWalletClient({
        transport: custom(provider)
      });
      
      console.log('钱包客户端创建完成');
      
      // 创建公共客户端 - 直接使用钱包提供者的 RPC，不指定自定义 RPC URL
      // 这样可以确保使用与钱包相同的网络
      this.publicClient = createPublicClient({
        transport: custom(provider)
      });
      
      console.log('公共客户端创建完成');
      return true;
    } catch (error: any) {
      console.error('初始化客户端失败:', error);
      console.error('错误详情:', {
        message: error.message,
        code: error.code,
        data: error.data
      });
      throw new Error(`初始化客户端失败: ${error.message}`);
    }
  }

  /**
   * 连接钱包
   * @returns 连接的地址
   */
  async connectWallet(timeoutMs: number = 15000): Promise<string> {
    // 确保客户端已初始化
    if (!this.wallet || !this.publicClient) {
      await this.initializeClients(timeoutMs);
    }

    try {
      console.log('请求连接钱包...');
      
      // 添加超时处理
      const withTimeout = <T>(promise: Promise<T>, ms: number, errorMessage: string): Promise<T> => {
        return Promise.race([
          promise,
          new Promise<T>((_, reject) => 
            setTimeout(() => reject(new Error(errorMessage)), ms)
          )
        ]);
      };
      
      const [address] = await withTimeout(
        this.wallet!.requestAddresses(),
        timeoutMs,
        '请求钱包地址超时，用户可能未响应钱包连接请求'
      );
      console.log('获取到钱包地址:', address);
      
      // 创建账户对象
      this.account = {
        address,
        type: 'json-rpc'
      };
      
      return address;
    } catch (error: any) {
      console.error('连接钱包失败:', error);
      throw new Error(`连接钱包失败: ${error.message}`);
    }
  }

  /**
   * 检查是否已连接钱包
   * @returns 是否已连接
   */
  isConnected(): boolean {
    return !!this.account;
  }

  /**
   * 获取当前连接的地址
   * @returns 连接的地址
   */
  getConnectedAddress(): string | undefined {
    return this.account?.address;
  }

  /**
   * 获取当前连接的链ID
   * @returns 链ID
   */
  getChainId(): number | undefined {
    return this.chainId;
  }

  /**
   * 获取钱包客户端
   * @returns 钱包客户端
   */
  getWallet(): WalletClient | undefined {
    return this.wallet;
  }

  /**
   * 获取公共客户端
   * @returns 公共客户端
   */
  getPublicClient(): PublicClient | undefined {
    return this.publicClient;
  }

  /**
   * 获取当前账户
   * @returns 账户
   */
  getAccount(): Account | undefined {
    return this.account;
  }

  /**
   * 确保钱包已连接，如果未连接则尝试连接
   * @returns 是否已连接
   */
  async ensureConnected(timeoutMs: number = 15000): Promise<boolean> {
    if (!this.isConnected()) {
      await this.connectWallet(timeoutMs);
    }
    return this.isConnected();
  }

  /**
   * 确保客户端已初始化，如果未初始化则尝试初始化
   * @returns 是否已初始化
   */
  async ensureInitialized(timeoutMs: number = 15000): Promise<boolean> {
    if (!this.publicClient || !this.wallet) {
      await this.initializeClients(timeoutMs);
    }
    return !!this.publicClient && !!this.wallet;
  }
} 