import { type Hash } from 'viem';
import { ERC20_ABI } from '../../config/contract';
import { BlockchainService } from '../blockchain';

/**
 * Token操作类
 * 负责处理ERC20代币的授权、检查等操作
 */
export class TokenOperations {
  private blockchainService: BlockchainService;

  constructor(blockchainService: BlockchainService) {
    this.blockchainService = blockchainService;
  }

  /**
   * 授权代币
   * @param tokenAddress 代币地址
   * @param spender 被授权者地址
   * @param amount 授权金额
   * @returns 交易哈希
   */
  async approveToken(tokenAddress: string, spender: string, amount: bigint): Promise<Hash> {
    await this.blockchainService.ensureWalletConnected();
    const wallet = this.blockchainService.getWalletClient();
    const account = this.blockchainService.getAccount();
    const chainId = this.blockchainService.getChainId();

    if (!wallet || !account || chainId === undefined) {
      throw new Error('钱包未连接或初始化失败');
    }

    console.log(`开始授权代币 ${tokenAddress} 给 ${spender}，金额: ${amount.toString()}`);

    const hash = await wallet.writeContract({
      account,
      chain: { id: chainId } as any,
      address: tokenAddress as `0x${string}`,
      abi: ERC20_ABI,
      functionName: 'approve',
      args: [spender as `0x${string}`, amount]
    });

    console.log('授权交易已发送，哈希:', hash);
    return hash;
  }

  /**
   * 检查授权额度
   * @param tokenAddress 代币地址
   * @param owner 代币持有者
   * @param spender 被授权者
   * @returns 当前授权额度
   */
  async checkAllowance(tokenAddress: string, owner: string, spender: string): Promise<bigint> {
    await this.blockchainService.ensureWalletConnected();
    const publicClient = this.blockchainService.getPublicClient();

    if (!publicClient) {
      throw new Error('公共客户端未初始化');
    }

    const allowance = await publicClient.readContract({
      address: tokenAddress as `0x${string}`,
      abi: ERC20_ABI,
      functionName: 'allowance',
      args: [owner as `0x${string}`, spender as `0x${string}`]
    }) as bigint;

    console.log(`当前授权额度: ${allowance.toString()}`);
    return allowance;
  }

  /**
   * 确保代币授权额度充足
   * 如果授权不足会自动发起授权交易
   * @param tokenAddress 代币地址
   * @param spender 被授权者
   * @param amount 需要的授权额度
   * @returns 是否成功确保授权
   */
  async ensureTokenAllowance(tokenAddress: string, spender: string, amount: bigint): Promise<boolean> {
    const owner = this.blockchainService.getConnectedAddress();
    if (!owner) {
      throw new Error('钱包未连接');
    }

    // 检查当前授权额度
    const currentAllowance = await this.checkAllowance(tokenAddress, owner, spender);
    
    console.log(`检查授权额度: 当前 ${currentAllowance.toString()}, 需要 ${amount.toString()}`);
    
    if (currentAllowance >= amount) {
      console.log('授权额度充足');
      return true;
    }

    console.log('授权额度不足，发起授权交易...');
    
    // 发起授权交易
    const approveHash = await this.approveToken(tokenAddress, spender, amount);
    
    // 等待交易确认
    const publicClient = this.blockchainService.getPublicClient();
    if (publicClient) {
      console.log('等待授权交易确认...');
      const receipt = await publicClient.waitForTransactionReceipt({ hash: approveHash });
      if (receipt.status !== 'success') {
        throw new Error('授权交易失败');
      }
      console.log('授权交易确认成功');
    }

    return true;
  }

  /**
   * 获取代币余额
   * @param tokenAddress 代币地址
   * @param owner 持有者地址，如不提供则使用当前连接的地址
   * @returns 代币余额
   */
  async getTokenBalance(tokenAddress: string, owner?: string): Promise<bigint> {
    await this.blockchainService.ensureWalletConnected();
    const publicClient = this.blockchainService.getPublicClient();

    if (!publicClient) {
      throw new Error('公共客户端未初始化');
    }

    const targetOwner = owner || this.blockchainService.getConnectedAddress();
    if (!targetOwner) {
      throw new Error('未指定查询地址且钱包未连接');
    }

    const balance = await publicClient.readContract({
      address: tokenAddress as `0x${string}`,
      abi: ERC20_ABI,
      functionName: 'balanceOf',
      args: [targetOwner as `0x${string}`]
    }) as bigint;

    console.log(`代币 ${tokenAddress} 余额: ${balance.toString()}`);
    return balance;
  }
} 